import React, { Component } from 'react';
import './App.css';
import {BrowserRouter as Router, Route, Redirect} from 'react-router-dom';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import {GetUser} from './services/userservice';
import { withAlert } from 'react-alert';
 
import Dashboard from './components/Dashboard';
import Authenticate from './components/Authenticate';


class App extends Component {
  
  constructor(props) {
    super(props);
    this.state = {
      user: null
    };
    this.assignUser = this.assignUser.bind(this);
  }

  componentDidMount() {
    GetUser(this.assignUser);
  }

  assignUser(err, user) {
    if (err && err.message !== "") {
      this.props.alert.show(err.message);
    }else{
      this.setState({ user: user.toObject()});
    }  
   
  }
  render() {
    const user = this.state.user
    if (user){                                                                                                                                                                                                                                                                                                                                                                          
      return (
        <Router>
            <ProtectedRoute path="/" user={this.state.user} exact component={Dashboard} />
            <Route path="/sign-in" exact component={Authenticate} />
        </Router>
      )
    }else {
      return (
        <Authenticate />
      )
    }
  }
  
}
// ProtectedRoute is a piece of borrowed code to reroute users if not logged in
const ProtectedRoute = ({ component: Comp, user, path, ...rest }) => {
  return (
    <Route
      path={path}
      {...rest}
      render={props => {
        return user ? (
          <Comp {...props} />
        ) : (
          <Redirect
            to={{
              pathname: "/sign-in",
              state: {
                prevLocation: path,
                error: "You need to login first!",
              },
            }}
          />
        );
      }}
    />
  );
};


export default withAlert()(App);
